<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8 text-color-red text-center">  ?</div>
            
            <div class="mt16 text-center text-size-ms">
                <div>
                    ,    , 
                <strong v-html="countSections"></strong>&nbsp;<strong
                                                                                          v-html="mfWord(countSections, [' ', ' ', ' '])"></strong>
                <br>
                <strong v-html="countActions"></strong>&nbsp;<strong
                                                                                         v-html="mfWord(countActions, ['', '', ''])"></strong>,
                    .<br>  ?
                </div>
            </div>

            <div class="mt32 text-center">
                <button class="btn" @click="apply">   !</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "CategoriesDelete",
        components: {},
        props: {
            countSections: {default: 0},
            countActions: {default: 0}
        },
        data: function () {
            return {}
        },
        created() {
        },
        mounted() {
        },
        methods: {
            apply: function () {
                this.$emit('apply');
            },
            cancel: function () {
                this.$emit('cancel');
            },
            mfWord: function (number, titles) {
                let cases = [2, 0, 1, 1, 1, 2];
                return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            })
        }
    }
</script>

