<template>
    <div class="header-category">
        <div class="inner-wrapper">
            <div class="header-category__menu">
                <div class="item" v-if="$route.params.action !== 'new' && $route.params.action !== 'wait_contract'">
                    <router-link active-class="active" :to="'/moderator/providers/'+$route.params.action+'/id'+$route.params.id+'/about/'"> </router-link>
                </div>
                <div class="item" v-if="$route.params.action !== 'new' && $route.params.action !== 'wait_contract'">
                    <router-link active-class="active" :to="'/moderator/providers/'+$route.params.action+'/id'+$route.params.id+'/actions/'"></router-link>
                </div>

                <div class="item" v-if="$route.params.action !== 'new' && $route.params.action !== 'wait_contract'">
                    <router-link active-class="active" :to="'/moderator/providers/'+$route.params.action+'/id'+$route.params.id+'/statistic/'"></router-link>
                </div>

                <div class="item" v-if="$route.params.action !== 'new'">
                    <router-link active-class="active" :to="'/moderator/providers/'+$route.params.action+'/id'+$route.params.id+'/contract/'"></router-link>
                </div>
                <div class="item" >
                    <router-link active-class="active" :to="'/moderator/providers/'+$route.params.action+'/id'+$route.params.id+'/history/'"></router-link>
                </div>
            </div>

            <div v-if="typeof(providerItem.about) !== 'undefined' && isProviderAboutSection" class="header-category__right">
                <a v-if="!providerItem.about.isFirstModeration && statusProvider !== 'wait' && statusProviderAbout === 'CHECK'" active-class="active" class="btn btn--ico btn--red size-s mr24" @click.prevent="openProviderPopup('rejectAbout')">
                    <span class="i i-close2"></span>
                    <span></span>
                </a>

                <a v-if="!providerItem.about.isFirstModeration && statusProvider !== 'wait' && statusProviderAbout === 'CHECK'" active-class="active" class="btn btn--ico btn--green size-s mr24" @click.prevent="openProviderPopup('approveAbout')">
                    <span class="i i-send"></span>
                    <span></span>
                </a>

                <a v-if="providerItem.about.isFirstModeration && statusProvider !== 'wait' && statusProviderAbout === 'CHECK'" active-class="active" class="btn btn--ico btn--red size-s mr24" @click.prevent="openProviderPopup('firstRejectAbout')">
                    <span class="i i-close2"></span>
                    <span></span>
                </a>

                <a v-if="providerItem.about.isFirstModeration && statusProvider !== 'wait' && statusProviderAbout === 'CHECK'" active-class="active" class="btn btn--ico btn--green size-s mr24" @click.prevent="openProviderPopup('firstApproveAbout')">
                    <span class="i i-send"></span>
                    <span></span>
                </a>

                <router-link active-class="active" class="btn btn--ico btn--white-half size-s " :to="editProviderLink">
                    <span class="i i-pencil"></span>
                    <span></span>
                </router-link>
            </div>
        </div>
        <!-- end inner-wrapper -->

    </div>
    <!-- end header-category -->
</template>

<script>
    import {mapState} from 'vuex';

    export default {
        name: "HeaderProviderMenu",
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                providerOperations: state => state.moderatorProviders.operations,
                user: state => state.users.user,
                providerLoading: state => state.moderatorProviders.isLoad,
                providerItem: state => state.moderatorProviders.itemDetail,
                supplier: state => state.supplier.itemDetail,
            }),
            statusProviderAbout() {
                let status;
                if (this.supplier.about) {
                    status = this.supplier.about.status;
                }

                return status;
            },
            statusProvider() {
                return this.$route.params.action;
            },
            isProviderEdit() {
                return this.$route.name === 'provider-edit'|| this.$route.name === 'provider-edit-section'
            },
            isProviderView() {
                return this.$route.name === 'provider-edit'|| this.$route.name === 'provider-edit-section'
            },
            successModerator(){
                let result = true;
                if(this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0){
                    result = false;
                    switch (this.$route.name){
                        case 'repayment':
                        case 'repayment-detail':
                        case 'repayment-default':
                            if(this.user.permissionModerator.indexOf(19) !== -1){
                                result = true;
                            }
                            break;
                    }
                }
                return result
            },
            providerContractLink(){
                let link = '';
                if (this.isProviderEdit) {
                    link = this.$store.getters['moderatorProviders/GetContractLink'];
                }
                return link;
            },
            isProviderEditLoaded() {
                return !this.providerLoading;
            },
            checkInnOgrn(){
                return this.$store.getters['moderatorProviders/GetCheckedStatusRequisites'];
            },
            isProviderAboutSection() {
                return (this.$route.name === 'provider-edit-section' && this.$route.params.section === 'about');
            },
            editProviderLink(){
                let link = '/moderator/partner-about-edit/id'+this.$route.params.id;
                if(this.$route.params.action){
                    link += '/'+this.$route.params.action+'/'
                }
                return link;
            }
        },
        methods: {
            openPrintContractError: function () {
                this.$store.dispatch('popups/open', {name: this.popups.printContract.name});
            },
            openProviderPopup: function (popup) {
                let self = this,
                    popupActions = {
                        rejectAbout: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerRejectAbout.name});
                        },
                        approveAbout: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerApproveAbout.name});
                        },
                        firstRejectAbout: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerFirstRejectAbout.name});
                        },
                        firstApproveAbout: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerFirstApproveAbout.name});
                        },
                        reject: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerReject.name});
                        },
                        approve: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerApprove.name});
                        },
                        print: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerPrint.name});
                        },
                        check_send: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerCheckSend.name});
                        },
                        final_approve: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerFinalApprove.name});
                        },
                        block: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerBlock.name});
                        },
                        cancel_reject: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerCancelReject.name});
                        },
                        cancel_block: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerCancelBlock.name});
                        },
                        overtime: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerOvertime.name});
                        },
                    }

                popupActions[popup]();
            },
        }
    }
</script>
