/**
 *    ,     
 * */

import axios from 'axios'
/**      axios
 * qs.stringify({ id: data.id }),
 */
import axiosCancel from 'axios-cancel';

axiosCancel(axios, {
    debug: false // default
});
import qs from 'qs';
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

let CancelToken = axios.CancelToken;

const state = {
    isLoad: false,
    itemDetail: {},
}

// getters
const getters = {
    getTitle: (state, getters, rootState, rootGetters) => id => {
        let title = '';
        return title;
    },
}

const actions = {
    getCurrent(context, data) {
        return new Promise((resolve, reject) => {
            if (!state.isLoad) {
                context.commit('setLoad', true);
                axios.get(process.env.VUE_APP_API_PATH + '/partner/current/', {
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                    withCredentials: true,
                }).then(response => {
                    context.commit('setSupplier', response.data.result.item);

                    context.commit('setLoad', false);
                    resolve();
                }).catch(function (error) {


                    context.commit('setLoad', false);
                    resolve();
                });
            } else {
                resolve();
            }
        });
    },
    initDetail(context, data) {
        return new Promise((resolve, reject) => {
            if (!state.isLoad) {
                let params = {
                    isModerator: data.isModerator,
                };

                context.commit('setLoad', true);
                axios.get(process.env.VUE_APP_API_PATH + '/public/supplier/' + data.route.params.id, {
                    withCredentials: true,
                    params: params,
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                }).then(response => {
                    context.commit('setSupplier', response.data.result);

                    context.commit('setLoad', false);
                    resolve();
                }).catch(function (error) {


                    context.commit('setLoad', false);
                    resolve();
                });
            } else {
                resolve();
            }
        });
    },
}


const mutations = {
    setSupplier(state, data) {
        Vue.set(state, 'itemDetail', data);
    },
    setSupplierImage(state, data) {
        let name = data.fieldName.replace('Media','Photos')
        if(data.fieldName == 'shopsMedia' || data.fieldName == 'officeMedia'){
            Vue.set(state.itemDetail.about[name],data.index, data.items);
        } else {
            Vue.set(state.itemDetail.about, name, data.items);
        }

    },
    setLoad(state, data) {
        Vue.set(state, 'isLoad', data);
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
