<template>
    <div class="h-nav-wrap">
            <draggable :forceFallback="true" :disabled="!canDrag || popups.addRule.show"
                        v-if="user.access_rights.is_owner" element="div"
                        v-model="topSections"
                        @start="dragStart"
                        @end="dragSave"
                        :move="dragStatus"
                        :animation="150"
                        :touchStartThreshold="3"
                        :delay="10"
                        class="h-nav"
            >
                <div v-for="section in topSections" class="h-nav-item" :key="'top_'+section.id">
                    <a @click.prevent="goToSection('/settings'+section.id+'/')" :href="'/settings'+section.id+'/'" class="i-wrapper edit-top-section" :class="{active: activeSection == section.id && $route.name == 'rules-category-settings' && isActiveMenu}">
                        <span class="i i-pencil2-active"></span>
                        <span class="i i-pencil2"></span>
                    </a>
                    <a @click.prevent="goToSection('/sid'+section.id+'/')" :href="'/sid'+section.id+'/'" class="h-nav-item-link h-link" :class="{active: activeSection==section.id && $route.name != 'rules-category-settings' && isActiveMenu}" :to="'/sid'+section.id+'/'">
                        <span>{{section.name}}</span>
                        <div class="update-notify"  v-if="!isEditing && section.updated && activeSection != section.id"></div>
                    </a>
                </div>
            </draggable>
            <div class="h-nav" v-else>
                <div v-for="section in topSections" class="h-nav-item" :key="'top_'+section.id">
                    <router-link class="h-nav-item-link h-link" :class="{active: activeSection == section.id && isActiveMenu}" :to="'/sid'+section.id+'/'">
                        <span>{{section.name}}</span>
                        <div class="update-notify" v-if="!isEditing && section.updated && activeSection!=section.id"></div>
                    </router-link>
                </div>
            </div>
        </div>
</template>
<script>
    import { mapState } from 'vuex'
    import draggable from '@/helpers/vuedraggable.js'
    export default {
        name: "TopSections",
        components: {
            draggable,
        },
        props: {
            return :{
                drag: false,
            },
        },
        mounted() {
            this.$store.dispatch('rules/getTopSection');
        },
        methods: {
            dragSave: function (e) {
                if (this.drag) {
                    this.$store.dispatch('rules/setTopSectionsSort', {});
                    setTimeout(()=>{
                        this.drag = false;
                    },20)
                }
            },
            dragStart: function (e) {
            },
            dragStatus: function (e) {
                this.drag = true;
            },
            goToSection(link){
                if(!this.drag){
                    this.$router.push(link)
                }
            }
        },
        computed: {
            ...mapState({
                user: state => state.users.user,
                canDragState: state => state.rules.canDrag,
                isSearch: state => state.rules.isSearch,
                popups: state => state.popups.items,
                isLoadItems: state => state.rules.isLoadItems,
                isLoadSections: state => state.rules.isLoadSections,
            }),
            curTopSection (){
                if(this.topSections[0]){
                    for (let i in this.topSections) {
                        if(this.topSections[i].id == this.activeSection){
                            return this.topSections[i];
                        }
                    }
                    return {};
                }
                else
                    return {};
            },
            canDrag(){
                if(this.user.access_rights.is_owner || (this.curTopSection && this.curTopSection.rights == 'W')) {
                    return this.canDragState
                } else {
                    return false
                }
            },
            isEditing() {
              return this.popups.addRule.show;
            },
            isActiveMenu (){
                return  this.$route.name != 'moderator-staff' && ( this.$route.name == 'rules-removed'  && this.popups.addRule.show || this.$route.name != 'rules-removed' );
            },
            activeSection(){
                if(this.isSearch) return false;
                if(this.is404) return false;
                return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
            },
            is404() {
                let topSection = this.topSections.find(sect => sect.id === this.$route.params.id);
                let subSectionId = false;

                if (this.$route.params.id) {
                    if (topSection) {
                        if (this.$route.params.subid) {
                            subSectionId = topSection.subsections.find(id => id === this.$route.params.subid);
                        } else {
                            subSectionId = true;
                        }
                    }
                } else {
                    subSectionId = true;
                }

                //  ,    ,    ,   
                //   
                let noSuccess = false;
                if (typeof this.topSections[0] !== 'undefined') {

                    if (!subSectionId && this.$route.params.id) {
                        if (typeof this.topSections[0].all[this.$route.params.id] !== 'undefined') {
                            if (this.$route.params.subid) {
                                if (typeof this.topSections[0].all[this.$route.params.id].find(id => id === this.$route.params.subid) !== 'undefined') {
                                    noSuccess = true;
                                }
                            } else {
                                noSuccess = true;
                            }
                        } else {
                            noSuccess = false;
                        }
                    }

                }

                return (this.$route.name === 'page404' || !subSectionId) && !noSuccess && !this.isLoadItems && !this.isLoadSections;
                //return (((this.$route.name === 'page404' || this.activeTopSectionName === '') && this.topSections.length > 0) || (this.activeTopSectionName !== '' && !subSectionId && this.sections.length > 0)) && !this.isSearch;
            },
            topSections: {
                get(){
                    return this.$store.getters['rules/getTopSections']
                },
                set(value){
                    this.$store.commit('rules/setTopSections', {
                        items: value
                    });
                }
            }
        }
    }
</script>
