<template>
    <div class="h-logo" v-if="isShowMenu">
        <router-link class="h-logo-link" to="/">
            <span class="icon"></span>
        </router-link>

        <tooltip ref="tooltipNotifyReset"
                 :direction="'bottom-center-direction'"
                 :add-class-wrap="'h-logo__tooltip'"
                 :isBtnClose="false"
                 :add-style="{width: 175 + 'px'}">
            <span slot="tooltipBtn"></span>

            <div slot="tooltipContent">
                <div class="text-center">
                    <div>
                        <a href="" class="i i-refresh2 i--blue"></a>
                    </div>
                    <div class="text-size-s" style="margin-top: 4px; ">
                          .<br>
                           <br>
                        <a href=""></a> 
                    </div>
                </div>
            </div>
        </tooltip>
    </div>
</template>

<script>
    import Tooltip from '@/components/Tooltip.vue'

    export default {
        name: "HeaderLogo",
        components: {
            Tooltip,
        },
        computed: {
            isShowMenu() {
                return (!(this.$route.name === 'cooperation-edit') &&
                    !(this.$route.name === 'provider-edit') &&
                    !(this.$route.name === 'providers-about-edit') &&
                    !(this.$route.name === 'partner-about-edit') &&
                    !(this.$route.name === 'coupons-about-edit') &&
                    !(this.$route.name === 'coupons-detail') &&
                    !(this.$route.name === 'coupons-edit') &&
                    !(this.$route.name === 'repayment-detail'));
            },
        }
    }
</script>

