<template>
  <div ref="searchField" class="header-category__search" v-clickoutside="hideSearchField" :class="{editor: isEditor, open: openSearch}">
    <div class="field-wrap">
      <input v-on:keyup.enter="fetchData" type="text" ref="searchFieldInput" id="searchFieldInput" class="field" placeholder=" "
             v-model="search" autocomplete="off">
      <span @click="resetSearch()" class="field-clear i i-close4"></span>
      <div class="b-wrapper">
          <button class="b" @click="fetchData"></button>
      </div>
    </div>

    <div ref="searchFieldBtn" @click="openSearchField" class="btn-search" :class="{'active': isSearch}">
        <span class="i i-search"></span>
    </div>
  </div>
</template>


<script>

import {mapState} from 'vuex';

export default {
  name: "RulesSearch",
  components: {},
  props: {
    isEditor:
        {
          default: function () {
            return false
          }
        }
  },
  data: function () {
    return {
      search: '',
      unsubscribe: '',
    }
  },
  mounted: function () {
    this.unsubscribe = this.$store.subscribe((mutation, state) => {
      if (mutation.type == 'setSearchFocus') {
        this.search = '';
        this.openSearchField();
      }
    });
  },
  beforeDestroy: function () {
    this.unsubscribe();
  },
  computed: {
    ...mapState({
      isSearch: state => state.rules.isSearch,
      popups: state => state.popups.items,
      item: state => state.rules.editItem,
      openSearch: state => state.rules.openSearchField,
      sections: state => state.rules.sections,
      topSections: state => state.rules.topSections,
    }),
    activeTopSection() {
      if (this.isSearch) return false;
      return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
    },
    activeSection() {
      if (this.sections.length) {
        return this.$route.params.subid ? this.$route.params.subid : this.sections[0].id;
      } else {
        return false;
      }
    },
  },
  methods: {
    resetSearch() {
      this.search = '';
      this.$store.commit('rules/setIsSearch', false);
      //        ,      ,   .
      //this.hideSearchField();
      this.fetchData(true);
    },
    fetchData(event, isReset) {
      if (this.search || isReset) {
        this.$store.dispatch('rules/getItems', {id: this.activeSection, search: this.search, reload: true});

        if (this.$router.history.current.fullPath !== '/') {
          this.$router.push({path: '/'});
        }

        this.hideSearchField();
      } else {
        this.$refs.searchFieldInput.focus();
      }
    },
    openSearchField: function () {
      this.$store.commit('rules/openSearchField', true);
      this.$refs.searchFieldInput.focus();
    },
    hideSearchField: function () {
      this.$store.commit('rules/openSearchField', false);
    }

  },
  created: function () {
    // document.addEventListener('click', e => {
    //     let target = e.target;
    //     let search = this.$refs.searchField;
    //     if (typeof(search) !== 'undefined') {
    //         let itsSearch = target == search || search.contains(target);

    //         if (!itsSearch) {
    //             this.hideSearchField();
    //         }
    //     }
    // })
  },

  directives: {
    'clickoutside': {
      bind: function (el, binding, vNode) {
        if (typeof binding.value !== 'function') {
          const compName = vNode.context.name;
          let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
          if (compName) {
            warn += `Found in component '${compName}'`
          }
          console.warn(warn);
        }
        const bubble = binding.modifiers.bubble;
        const handler = (e) => {
          if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-search'))) {
            binding.value(e)
          }
        }
        el.__vueClickOutside__ = handler;
        document.addEventListener('mousedown', handler);
      },

      unbind: function (el, binding) {
        document.removeEventListener('mousedown', el.__vueClickOutside__);
        el.__vueClickOutside__ = null;
      }
    }
  }
}
</script>
