<template>
    <div class="all-wrapper grey-wrapper" id="app" v-show="isUserLoad"
         :class="{'no-stickers': true, 'page-auth': !isModerator || isNoSuccessSection, 'staff-page': isStaffPage, 'rule-edit': popups.addRule.show, 'admin-editable': tumblers.editable.value && user.access_rights.is_owner, 'section-detail-page': isSectionDetail, 'editor-editable': tumblers.editable.value && (curTopSection && curTopSection.rights == 'W') && !popups.addRule.show}">
        <header v-if="isModerator && !isNoSuccessSection" :class="{'header-moderator --moderator': !isActionEditPage && !isRepaymentDetail && !isCooperationEdit && !isProviderEdit && !isProviderConstruct && !isCouponDetail,  '--auth': !isModerator}">
            <div class="header-main">
                <div class="inner-wrapper">
                    <div class="h-left">
                        <header-logo></header-logo>
                        <top-sections v-if="isUserLoad"></top-sections>
                        <router-link v-if="user.access_rights.is_owner && deactivatedItems > 0 && !isLoadItems" :to="'/removed/'" class="removed-link ml8" :class="{active: $route.name == 'rules-removed'}">
                            <span class="i-trash-wrapper">
                                <span class="i i-trash-active" v-if="$route.name == 'rules-removed'"></span>
                                <span class="i i-trash" v-else></span>
                            </span>
                            <span class="update-notify" v-if="!isEditing && $route.name != 'rules-removed'"></span>
                        </router-link>
                        <div class="removed-link ml8" style="position: relative;" v-else-if="user.access_rights.is_owner && deactivatedItems > 0 && isLoadItems">
                          <span class="i-trash-wrapper">
                                <span class="i i-trash-active" v-if="$route.name == 'rules-removed'"></span>
                                <span class="i i-trash" v-else></span>
                            </span>
                          <span class="update-notify" v-if="!isEditing && $route.name != 'rules-removed'"></span>
                        </div>
                        <div v-if="user.access_rights.is_owner" class="add-nav-item__wrap">
                            <div class="add-nav-item" @click="openSectionAdd">
                                <span class="i i-plus8"></span>
                                <span class="i i-plus8-active"></span>
                            </div>
                        </div>
                    </div>
                    <!-- end h-left -->

                    <div class="rule-edit__close" v-if="popups.addRule.show" @click="closeAddRule">
                        <span class="i i-close9"></span>
                    </div>
                    <div class="h-right">
                        <rules-search :class="{isEditor: user.access_rights.is_owner}"></rules-search>
                        <router-link v-if="user.access_rights.is_owner" @click.native="resetSearch();" href="/rules/staff/" :to="'/staff/'" class="staff-link" :class="{active: isStaffPage && !isSearch}">
                            <span class="i-staff-wrapper">
                                <span class="i i-staff-active" v-if="isStaffPage && !isSearch"></span>
                                <span class="i i-staff" v-else></span>
                            </span>
                        </router-link>
                        <div v-if="isShowMenu">
                            <tooltip v-if="user.firstName"
                                     :direction="'bottom-right-direction'"
                                     :add-class-wrap="'h-user'"
                                     :add-class-btn="'l'"
                                     :btn-big="true"
                                     @tooltipShow="userTooltipActive = true"
                                     @tooltipClose="userTooltipActive = false"
                                     :add-class="'h-user-menu'">
                                <span slot="tooltipBtn">
                                    <span class="t">
                                        {{ user.firstName.charAt(0) }}
                                    </span>
                                </span>

                                <div slot="tooltipContent">
                                    <div class="h-user-menu__item">
                                        <span class="t">{{ user.firstName }} {{ user.lastName }}</span>
                                    </div>

                                    <div class="h-user-menu__item">
                                        <span class="t">{{ user.phone }}</span>
                                    </div>

                                    <div class="h-user-menu__bottom">
                                        <button class="btn size-s btn--ico" @click="logout()">
                                            <span class="i i-exit2"></span>
                                            <span class="t"></span>
                                        </button>
                                    </div>
                                </div>
                            </tooltip>
                        </div>
                    </div>
                </div>
                <!-- end inner-wrapper -->
            </div>
            <!-- end header__bottom -->
            <header-provider-menu @apply="applyProviderAbout" @reject="rejectProviderAbout" v-if="isProviderEdit"></header-provider-menu>

        </header>


        <main id="main" :class="{'provider-detail': isProviderEdit, 'provider-about': $route.params.section == 'about', 'main-coupon-detail': isCouponDetail}">
            <router-view v-if="isModerator && successModerator && !isNoSuccessSection"/>

            <section v-else>
                <div class="content">

                    <router-link @click.native="setIsNoSuccessSection()" to="/" tag="div" style="cursor: pointer;" class="mt40 text-center" v-if="isNoSuccessSection">
                        <img src="@/assets/img/logo.svg" alt="">
                    </router-link>
                    <div class="mt40 text-center" v-else>
                        <img src="@/assets/img/logo.svg" alt="">
                    </div>

                    <section class="container mt40">
                        <div class="auth-form-container">
                            <auth v-if="!token && !isBanned && isValidPhone && !isNoSuccess && !isNoSuccessSection"></auth>


                            <div class="auth-form" v-else-if="isBanned && !isNoSuccess">
                                <div class="auth-form__error">
                                    <div class="text-center">
                                        <svg class="big-icon" >
                                            <use xlink:href="@/assets/img/big-icons-sprite.svg#many-auth"></use>
                                        </svg>
                                    </div>

                                    <div class="mt32 text-center">
                                        <div class="text-size-l text-bold text-color-red2">   !</div>
                                        <div class="mt16 text-size-ms">
                                               12 .    - ,<br>
                                               <a href="mailto:zems@zems.pro">zems@zems.pro</a>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="auth-form" v-else-if="!isNoSuccessSection">
                                <div class="auth-form__error">
                                    <div class="text-center">
                                        <div>
                                            <svg class="big-icon" >
                                                <use xlink:href="@/assets/img/big-icons-sprite.svg#cat"></use>
                                            </svg>
                                        </div>

                                        <div class="text-size-l text-bold text-color-red2 mt32">
                                              {{(user.phone)?user.phone:formAuthFields.login ? formAuthFields.login.value : ''}}    
                                        </div>
                                        <div class="mt16 text-size-ms">
                                               ,<br>
                                               <a href="mailto:zems@zems.pro">zems@zems.pro</a>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="auth-form" v-else>
                                <div class="auth-form__error">
                                    <div class="text-center">
                                        <div>
                                            <svg class="big-icon" >
                                                <use xlink:href="@/assets/img/big-icons-sprite.svg#cat"></use>
                                            </svg>
                                        </div>

                                        <div class="text-size-l text-bold text-color-red2 mt32">
                                              {{(user.phone)?user.phone:''}}  <br>
                                              
                                        </div>
                                        <div class="mt16 text-size-ms">
                                               ,<br>
                                               <a href="mailto:zems@zems.pro">zems@zems.pro</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
                <vue-footer v-if="!isNoSuccessSection" :class="{'footer--center': !(isModerator && successModerator)}" :show-logo="isModerator && successModerator" :hide-offer="true"></vue-footer>
            </section>

        </main>
        <popup :is-set-styled="false" :show="popups.sectionAdd.show" :name="popups.sectionAdd.name" :width="400" :add-class="'add-cat add-cat-uppercase'">
            <section-add></section-add>
        </popup>

        <popup :is-set-styled="false" :show="popups.sectionRemoveError.show" :name="popups.sectionRemoveError.name" :width="400" :is-btn-close="false" >
            <section-remove-error></section-remove-error>
        </popup>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import TopSections from '@/components/moderator/Rules/TopSections.vue'


    import Popup from '@/components/modal/Popup.vue'
    import SendAllProviders from '@/components/modal/SendAllProviders.vue'
    import Auth from '@/components/user/auth/AuthPersonal.vue'
    import Tooltip from '@/components/Tooltip.vue'

    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import CategoryChoice from '@/components/forms/CategoryChoice.vue'
    import {scrollTo} from '@/helpers/animate.js';
    import VueFooter from '@/components/VueFooter.vue'
    import HeaderLogo from '@/components/moderator/header/HeaderLogo.vue'
    import HeaderEditorBack from '@/components/moderator/header/HeaderEditorBack.vue'
    import HeaderProviderMenu from '@/components/moderator/header/HeaderProviderMenu.vue';
    import ZemsButton from '@/components/ZemsButton.vue';
    import draggable from '@/helpers/vuedraggable.js'
    import RulesSearch from '@/components/moderator/RulesSearch.vue';
    import SectionAdd from '@/components/modal/rules/SectionAdd.vue';
    import SectionRemoveError from '@/components/modal/rules/SectionRemoveError.vue';

    export default {
        components: {
            TopSections,
            CategoryChoice,
            Popup,
            SendAllProviders,
            Auth,
            Tooltip,

            SubmitBtn,
            VueFooter,
            HeaderLogo,
            HeaderEditorBack,
            HeaderProviderMenu,
            ZemsButton,
            draggable,
            RulesSearch,
            SectionAdd,
            SectionRemoveError
    },
        created() {
            this.$store.dispatch('users/getCurrent').then(() => {
                if (!this.successModerator) {
                    if (this.user.permissionModerator.indexOf(19) !== -1) {
                        this.$router.push({path: '/moderator/repayment/'});
                    }
                }
            });
            this.$store.dispatch('cities/setCityCookies');
            //this.$store.dispatch('cities/getCities');
            this.$store.dispatch('options/getList');
            this.$store.dispatch('rules/getMarks');
            if (window.location.hash === '#open_catalog' && this.successModerator) {
                this.openCategoryChoice()
            }
        },
        mounted() {
            this.$store.subscribe((mutation, state) => {
                if (mutation.type == 'popups/open') {
                    if (state.popups.items.notifyPageReset.show == true) {
                        this.showNotifyPageReset();
                    }
                }
            });

            let scrollWidth = window.innerWidth - document.documentElement.clientWidth;
            if (scrollWidth == '0') {
                document.getElementsByTagName('body')[0].classList.add("scroll-mac-os");
            }
        },
        data() {
            return {
                buttons: {
                    saveAction: {
                        state: 'normal'
                    },
                    savePartner: {
                        state: 'normal'
                    },
                    approveEdit: {
                        state: 'normal',
                    },
                    rejectEdit: {
                        state: 'normal',
                    }
                },
                userTooltipActive: false,
                showBlockBtn: false,
            }
        },
        computed: {
            inspectionBtnShow() {
                return (this.isModerator && this.successModerator || this.isBoss) && (
                    this.$route.name === 'coupons-detail' || this.$route.name === 'coupons-edit'
                    || this.$route.name === 'provider-edit-section' || this.$route.name === 'partner-about-edit'
                )
            },
            isActionRework(){
                return this.providerItem && this.providerItem.statusInspection === 'rework';
            },
            isProviderRework(){
                return this.action && this.action.statusInspection === 'rework';
            },
            isCooperationEdit() {
                return this.$route.name === 'cooperation-edit'
            },
            isActionEditPage() {
                return this.$route.name === 'coupons-edit'
            },
            isRulesPage() {
                return this.$route.name === 'moderator-rules' || this.$route.name === 'moderator-rules-category'
            },
            isCooperationEditLoaded() {
                if ((this.cooperationItem.length !== 0) && !this.cooperationLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isCouponDetail() {
                return this.$route.name === 'coupons-detail'
            },
            isStaffPage() {
                return this.$route.name === 'moderator-staff'
            },
            isSectionDetail() {
                return this.$route.name === 'rules-category-settings'
            },
            isRepaymentDetail() {
                return this.$route.name === 'repayment-detail'
            },
            isProviderEdit() {
                return this.$route.name === 'provider-edit' || this.$route.name === 'provider-edit-section'
            },
            isProviderConstruct() {
                return this.$route.name === 'coupons-about-edit' || this.$route.name === 'providers-about-edit' || this.$route.name === 'partner-about-edit'
            },
            cancelProviderAboutEdit() {
                if (this.$route.name === 'coupons-about-edit') {
                    return '/moderator/coupons/' + this.actionBackCode + '/id' + this.$route.params.id + '/'
                } else {
                    if(this.$route.params.status){
                        return '/moderator/providers/' + this.$route.params.status + '/id' + this.$route.params.id + '/about/'
                    } else {
                        return '/moderator/providers/' + this.providerBackCode + '/id' + this.$route.params.id + '/about/'
                    }
                }
            },
            isProviderEditLoaded() {
                return !this.providerLoading;
            },
            isCouponOperationsLoaded() {
                return (this.operations.length !== 0) && !this.couponLoading;
            },
            isRepaymentDetailLoaded() {
                return (this.repayment.length !== 0) && !this.repaymentLoading;
            },
            isShowMenu() {
                return (!(this.$route.name === 'cooperation-edit') &&
                    !(this.$route.name === 'provider-edit') &&
                    !(this.$route.name === 'provider-edit-section') &&
                    !(this.$route.name === 'providers-about-edit') &&
                    !(this.$route.name === 'partner-about-edit') &&
                    !(this.$route.name === 'coupons-about-edit') &&
                    !(this.$route.name === 'coupons-detail') &&
                    !(this.$route.name === 'coupons-edit') &&
                    !(this.$route.name === 'repayment-detail'));
            },




            providerBackId() {
                let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                return statusId;
            },

            pageRoute() {
                return this.$route.name;
            },
            userCheck() {
                if (!!this.user.firstName /*&& !!this.user.secondName && !!this.user.lastName && !!this.user.email*/) {
                    return true;
                }
                return false;
            },
            providerContractLink() {
                let link = '';
                if (this.isProviderEdit) {
                    link = this.$store.getters['moderatorProviders/GetContractLink'];
                }
                return link;
            },
            checkInnOgrn() {
                let isSuccess = this.$store.getters['moderatorProviders/GetCheckedStatusRequisites'];
                return isSuccess;
            },
            isModerator() {
                if (this.user.access_rights) {
                    return this.user.access_rights.is_moderator;
                }
                else {
                    return false;
                }
            },
            isBoss() {
                if (this.user.access_rights) {
                    return this.user.access_rights.is_boss;
                }
                else {
                    return false;
                }
            },




            token() {
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            successModerator() {
                let result = true;
                if (this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0) {
                    result = false;
                    switch (this.$route.name) {
                        case 'repayment':
                        case 'repayment-detail':
                        case 'repayment-default':
                            if (this.user.permissionModerator.indexOf(19) !== -1) {
                                result = true;
                            }
                            break;
                    }
                }
                return result
            },
            showMenuRightIcon() {
                let result = true;
                if (this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0) {
                    if (this.user.permissionModerator.indexOf(19) !== -1) {
                        result = false;
                    }
                }
                return result
            },
            isEditing() {
              return this.popups.addRule.show;
            },
            activeSection() {
                if (this.sections.length) {
                    return this.$route.params.subid ? this.$route.params.subid : this.sections[0].id;
                } else {
                    return false;
                }
            },
            ...mapState({
                isSearch: state => state.rules.isSearch,
                isSearchPrev: state => state.rules.isSearchPrev,
                searchStr: state => state.rules.searchStr,
                isLoadItems: state => state.rules.isLoadItems,
                deactivatedItems: state => state.rules.deactivatedItems,
                popups: state => state.popups.items,
                user: state => state.users.user,
                formAuthFields: state => state.auth.forms.auth.fields,
                isValidPhone: state => state.auth.isValidPhone,
                cooperationItem: state => state.moderatorCooperation.itemDetail,
                cooperationLoading: state => state.moderatorCooperation.isLoad,
                providerItem: state => state.partner.current,
                providerListStatus: state => state.moderatorProviders.listStatus,
                repaymentListStatus: state => state.moderatorRepayment.listStatus,
                actionListStatus: state => state.moderatorCoupon.listStatus,
                cooperationListStatus: state => state.moderatorCooperation.listStatus,
                providerLoading: state => state.moderatorProviders.isLoad,
                couponLoading: state => state.moderatorCoupon.isLoadDetail,
                topSections: state => state.rules.topSections,
                isBanned: state => state.auth.isBanned,
                cookiesWarning: state => state.users.cookiesWarning,
                currentCity: state => state.cities.current,
                currentCityIp: state => state.cities.currentCityIp,
                operations: state => state.moderatorCoupon.operations,
                providerOperations: state => state.moderatorProviders.operations,
                action: state => state.moderatorCoupon.itemDetail,
                actionLoading: state => state.moderatorCoupon.isLoadDetail,
                isUserLoad: state => state.users.isLoaded,
                repayment: state => state.moderatorRepayment.itemDetail,
                repaymentLoading: state => state.moderatorRepayment.isLoadDetail,
                repaymentOperations: state => state.moderatorRepayment.operations,
                currentStep: state => state.partnerActions.currentStep,
                status: state => state.moderatorCoupon.status,
                isNoSuccess: state => state.auth.isNoSuccess,
                isNoSuccessSection: state => state.auth.isNoSuccessSection,
                blockEdit: state => state.moderatorCoupon.blockEdit,
                item: state => state.rules.editItem,
                tumblers: state => state.rules.tumblers,
                previousSection: state => state.rules.previousSection,
            }),
            activeTopSection(){
              if(this.isSearch || !this.topSections.length) return false;
              return this.$route.params.id ? this.$route.params.id : this.topSections[0].id;
            },
            curTopSection (){
              if(!this.topSections[0]) return false;

              for (let i in this.topSections) {
                if(this.topSections[i].id == this.activeTopSection){
                  return this.topSections[i];
                }
              }
            },
        },
        methods: {
            resetSearch: function (e) {
                this.$store.commit('rules/setIsSearch',false);
            },
            setIsNoSuccessSection: function (e) {
                this.$store.commit('auth/setIsNoSuccessSection',false)
            },
            dragSave: function (e) {
                if (this.drag) {
                    this.drag = false;
                }
            },
            dragStart: function (e) {
            },
            dragStatus: function (e) {
                this.drag = true;
            },
            openPrintContractError: function () {
                this.$store.dispatch('popups/open', {name: this.popups.printContract.name});
            },
            openAuthorization: function () {
                this.$store.dispatch('auth/setIsRedirect', true);
                this.$store.dispatch('popups/open', {name: this.popups.publicAuth.name});
            },
            openRequestApprove: function () {
                this.$store.dispatch('popups/open', {name: this.popups.requestApprove.name});
            },
            openRequestReject: function () {
                this.$store.dispatch('popups/open', {name: this.popups.requestReject.name});
            },
            openRequestPause: function () {
                this.$store.dispatch('popups/open', {name: this.popups.requestPause.name});
            },
            routeCouponEdit: function ($id) {
                this.$store.dispatch('moderatorCoupon/checkBlockEdit', {id: $id}).then( (e) => {
                    if(e.status === true) {
                        this.$store.dispatch('popups/open', {name: this.popups.blockEdit.name});
                    } else {
                        let link = '/moderator/coupons/edit'+$id+'/';
                        if(this.$route.params.action){
                            link += this.$route.params.action+'/'
                        }
                        this.$router.push({path: link});
                    }
                });

            },
            routeCouponSection($id) {
                this.$router.push({path: '/moderator/coupons/section' + $id + '/'});
            },
            openCouponBlock: function () {
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponBlock.name});
            },
            openCouponUnBlock: function () {
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponUnBlock.name});
            },
            openSectionAdd: function () {
                this.$store.commit('rules/setEditSection',[]);
                this.$store.dispatch('popups/open', {name: this.popups.sectionAdd.name});
            },
            openCategoryChoice: function (isSetCategory) {
                if (this.$router.history.current.name === 'coupons-detail' || !!isSetCategory) {
                    this.$store.dispatch('moderatorCategories/setItem', {
                        id: this.action.id,
                        name: this.action.name
                    });
                }

                this.$store.dispatch('popups/open', {name: this.popups.categoryChoice.name});
                this.$nextTick(() => {
                        document.getElementsByTagName('body')[0].classList.add("city-select");
                    }
                );
            },
            openCouponApprove: function () {
                if (this.action.sections.length == 0) {
                    this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponAlert.name});
                }
                else {
                    this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponApprove.name});
                }
            },
            openCouponReject: function () {
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponReject.name});
            },
            openModalSendAllProviders: function () {
                this.$store.dispatch('popups/open', {name: this.popups.sendAllProviders.name});
            },
            openProviderPopup: function (popup) {
                let self = this,
                    popupActions = {
                        reject: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerReject.name});
                        },
                        approve: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerApprove.name});
                        },
                        print: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerPrint.name});
                        },
                        check_send: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerCheckSend.name});
                        },
                        final_approve: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerFinalApprove.name});
                        },
                        block: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerBlock.name});
                        },
                        cancel_reject: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerCancelReject.name});
                        },
                        cancel_block: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerCancelBlock.name});
                        },
                        overtime: function () {
                            self.$store.dispatch('popups/open', {name: self.popups.providerOvertime.name});
                        },
                    }

                popupActions[popup]();
            },
            openCouponCheating: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponCheating.name});
            },
            openCouponExtend: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponExtend.name});
            },
            openCouponAnnul: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponAnnul.name});
            },
            openUserBan: function () {
                this.$store.dispatch('popups/open', {name: this.popups.userBan.name});
            },
            openUserUnBan: function () {
                this.$store.dispatch('popups/open', {name: this.popups.userUnBan.name});
            },
            rejectEdit: function () {
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponRejectEdit.name});

            },
            approveEdit: function () {
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponApproveEdit.name});

            },
            logout() {
                this.$store.dispatch('auth/logout');
                this.$store.commit('auth/setStep', {step: 1})

            },
            saveAction() {
                if (this.currentStep !== 'finish') {
                    this.$store.dispatch('partnerActions/nextStep')
                } else {
                    this.buttons.saveAction.state = 'load';

                    this.$store.dispatch('partnerActions/saveAction', {
                        id: this.$route.params.id,
                        isModerator: true
                    }).then(() => {
                        this.buttons.saveAction.state = 'ok';
                        setTimeout(() => {
                            this.$router.push('/moderator/coupons/' + this.actionBackCode + '/id' + this.$route.params.id);
                            this.buttons.saveAction.state = 'normal';
                        }, 300);
                    }, () => {
                        this.buttons.saveAction.state = 'normal';
                    })
                }
            },
            saveAboutForm() {
                this.buttons.savePartner.state = 'load';
                this.$store.dispatch('partner/saveAboutForm', {isModerator: this.user.access_rights.is_moderator}).then((data) => {
                    this.buttons.savePartner.state = 'ok';
                    setTimeout(() => {
                        if (this.$route.name === 'coupons-about-edit') {
                            this.$router.push('/moderator/coupons/' + this.actionBackCode + '/id' + this.$route.params.id);
                        } else {
                            this.$router.push('/moderator/providers/' + this.providerBackCode + '/id' + this.$route.params.id + '/about/');
                        }
                        this.buttons.savePartner.state = 'normal';
                    }, 300);

                }).catch((e) => {
                    setTimeout(() => {
                        this.buttons.savePartner.state = 'normal';
                    }, 300);
                    if (!!e.scrollTo) {
                        if (e.scrollTo == 'otherTitle') {
                            scrollTo('#scroll-other');
                        } else {
                            scrollTo('#scroll-' + e.scrollTo);
                        }

                    }
                });
            },
            showNotifyPageReset() {
                this.$refs.tooltipNotifyReset.tooltipShow();
            },
            applyProviderAbout() {
                //console.log('apply');
            },
            rejectProviderAbout() {
                //console.log('reject');
            },
            closeAddRule: function () {
                this.$store.commit('rules/setSelectedSection', true);
                if(this.isSearchPrev){
                    this.$store.commit('rules/setIsSearch', true);
                    this.$store.commit('rules/setIsSearchPrev', false);
                    this.$store.dispatch('rules/getItems', {search: this.searchStr, reload: true});
                    if (this.$router.history.current.fullPath !== '/') {
                        this.$router.push({path: '/'});
                    }
                } else {
                    if(this.item.id && !this.item.active){
                        this.$router.push('/removed/');
                    } else if(this.item.id && this.item.active){
                        if(this.$route.path != '/sid'+this.item.sectionTop+'/sub'+this.item.section+'/'){
                            this.$router.push('/sid'+this.item.sectionTop+'/sub'+this.item.section+'/');
                        }

                    } else if(this.previousSection.top.id && this.previousSection.second.id){
                        if(this.$route.path != '/sid'+this.previousSection.top.id+'/sub'+this.previousSection.second.id+'/'){
                            this.$router.push('/sid'+this.previousSection.top.id+'/sub'+this.previousSection.second.id+'/');
                        }
                    }
                }

                this.$store.commit('popups/close', {name: this.popups.addRule.name});
            },
            closeBlockEdit: function () {

                this.$store.commit('popups/close', {name: this.popups.blockEdit.name});

                if(this.$route.path !== '/moderator/coupons/'+this.actionBackCode+'/id'+this.$route.params.id+'/'){

                    this.$router.push('/moderator/coupons/'+this.actionBackCode+'/id'+this.$route.params.id+'/');

                }

            },


        }
    }
</script>
