<template>
    <div>
        <div class="zems-button" @click="openNotes" :style="{top: top + 'px', zIndex: zIndex}">

        </div>
        <div class="zems-notes-block" :class="{'move-left': show}">
            <submit-btn v-if="isInspection"
                        tag="button"
                        @click.native="send('rework')"
                        :type="buttons.rework.state"
                        class="size-s btn--ico mr16">
                <span v-if="buttons.rework.state === 'normal'" class="i i-send i--white"></span>
                <span class="text">  </span>
            </submit-btn>
            <submit-btn v-else
                        tag="button"
                        @click.native="send('review')"
                        :type="buttons.review.state"
                        class="size-s btn--ico mr16">
                <span v-if="buttons.review.state === 'normal'" class="i i-send i--white"></span>
                <span class="text">  </span>
            </submit-btn>
            <submit-btn v-if="isBoss"
                        tag="button"
                        @click.native="send('approve')"
                        :type="buttons.approve.state"
                        class="btn btn--green btn--ico size-s" style="height: 32px !important; width: 95px !important;">
                <span v-if="buttons.approve.state === 'normal'" class="i i-ok i--white"></span>
                <span class="text"></span>
            </submit-btn>
            <span class="i i-close" @click="closeNotes(true)"></span>
            <textarea-field @input="setTmpVersion" v-model="text" ></textarea-field>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import TextareaField from '@/components/forms/TextareaField.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "ZemsButton",
        components: {
            TextareaField,
            SubmitBtn,
        },
        data: function() {
            return {
                show: false,
                buttons: {
                    approve: {
                        state: 'normal'
                    },
                    rework: {
                        state: 'normal'
                    },
                    review: {
                        state: 'normal'
                    },
                },
                text: ''
            }
        },
        props: {
            top: {default: 64},
            zIndex: {default: 10000}
        },
        methods: {
            openNotes(){
                this.show = true;
                this.text = this.inspectionItem.inspection ? this.inspectionItem.inspection.text : '';
                this.loadTmpVersion();
            },
            closeNotes(isSetText){
                this.show = false;
                //this.text = '';
                if(isSetText){
                    this.$store.dispatch('inspection/setText', {
                        entity_id: this.inspectionItem.id,
                        type: this.typeItem,
                        text: this.text,
                        isEdit: this.isEdit,
                    })
                }
            },
            setTmpVersion() {
                if (this.text) {
                    this.$store.dispatch('inspection/setTmpVersion', {
                        entity_id: this.inspectionItem.id,
                        text: this.text,
                    })
                }
            },
            loadTmpVersion() {
                console.log(this.inspection.inspectionTmp[this.inspectionItem.id]);
                if (this.inspection.inspectionTmp[this.inspectionItem.id]) {
                    this.text = this.inspection.inspectionTmp[this.inspectionItem.id];
                }
            },
            send(status){
                if(this.text.length>0 || status === 'approve'){
                    this.buttons[status].state = 'load';
                    this.$store.dispatch('inspection/setStatus', {
                        entity_id: this.inspectionItem.id,
                        type: this.typeItem,
                        text: this.text,
                        isEdit: this.isEdit,
                        status: status
                    }).then(() => {
                        this.$store.dispatch('inspection/clearTmpVersion', {
                            entity_id: this.inspectionItem.id
                        });
                        this.buttons[status].state = 'ok';
                        setTimeout(()=>{
                            this.buttons[status].state = 'normal';
                            this.closeNotes(false);
                            switch (this.$route.name) {
                                case 'coupons-detail':
                                    if(status === 'rework'){
                                        this.$router.push('/moderator/coupons/inspection/')
                                    }
                                    if(status === 'review'){
                                        this.$router.push('/moderator/coupons/inspection-rework/')
                                    }
                                    break;
                                case 'coupons-edit':

                                    break;
                                case 'provider-edit-section':
                                    if(status === 'rework'){
                                        this.$router.push('/moderator/providers/inspection/')
                                    }
                                    if(status === 'review'){
                                        this.$router.push('/moderator/providers/inspection-rework/')
                                    }
                                    break;
                                case 'partner-about-edit':

                                    break;
                            }

                        }, 300);
                    });
                }
            }
        },
        watch: {
            isOnTop(){
                this.zIndex =  0;
            }
        },
        computed:{
            ...mapState({
                user: state => state.users.user,
                providerItem: state => state.moderatorProviders.itemDetail,
                provider: state => state.partner.current,
                actionPartner: state => state.partnerActions.detail,
                action: state => state.moderatorCoupon.itemDetail,
                inspection: state => state.inspection,
            }),
            inspectionItem(){
                let item = {};
                switch (this.$route.name) {
                    case 'coupons-detail':
                        item = this.action;
                        break;
                    case 'coupons-edit':
                        item = this.actionPartner;
                        break;
                    case 'provider-edit-section':
                        item = this.providerItem;
                        break;
                    case 'partner-about-edit':
                        item = this.provider;
                        break;
                }
                return item;
            },
            typeItem(){
                if(this.$route.name === 'coupons-detail' || this.$route.name === 'coupons-edit'){
                    return 'action'
                } else {
                    return 'provider'
                }
            },
            isEdit(){
                return this.$route.name === 'partner-about-edit' || this.$route.name === 'coupons-edit'
            },
            isBoss() {
                if (this.user.access_rights) {
                    return this.user.access_rights.is_boss;
                }
                else {
                    return false;
                }
            },
            isInspection() {
                let isInspection = false;

                if (typeof(this.$route.params) !== 'undefined' && this.$route.params.action === 'inspection') {
                    isInspection = true;
                } else if(typeof(this.inspectionItem.inspection) !== 'undefined' && this.inspectionItem.inspection.status === 'review'){
                    isInspection = true;
                }

                return isInspection;
            },
            isInspectionRework() {
                let isInspection = false;

                if (typeof(this.$route.params) !== 'undefined' &&
                    this.$route.params.action === 'inspection-rework') {
                    isInspection = true;
                }

                return isInspection;
            },
        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }


</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: opacity .15s;
    }
    .fade-popup-enter, .fade-popup-leave-to {
        opacity: 0;
    }
</style>
