// getters
const getters = {

}

const actions = {
    open(context, data) {
        if(data.name && state.items.hasOwnProperty(data.name)) {
            setTimeout(()=>{
                context.commit('open', {name:data.name});
            },10);
        }
    },
}

const mutations = {
    close(state, data) {
        if(data.name && state.items.hasOwnProperty(data.name)) {
            state.items[data.name].show = false;
            document.getElementsByTagName('body')[0].classList.remove("noscroll");
        }
    },
    open(state, data) {
        if(data.name && state.items.hasOwnProperty(data.name)) {
          // !!          
          //setTimeout(function(){
            state.items[data.name].show = true
          //},10);
        }
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
