<template>
    <div class="w-tooltip"
        :class="[{'opened': show}, addClassWrap]"
         ref="tooltipWrap"
         v-clickoutside="tooltipClose">
        <span ref="tooltipBtn" class="tooltip__btn" @click.prevent="btnClick" :class="addClassBtn" @mouseover="mouseOver" @mouseleave="mouseLeave">
            <slot name="tooltipBtn"></slot>
        </span>

        <transition name="fade-popup">
            <div class="tooltip"
                 ref="tooltip"
                 v-if="show"
                 :class="[addClass, tooltipDirection, {'--close-btn-big': btnBig}]"
                 :style="addStyle">
                <div v-if="isBtnClose" class="tooltip__close" @click.prevent="btnClick"></div>
                <div class="tooltip__content" :style="{'padding':!isPadding ? '0': ''}">
                    <slot name="tooltipContent"></slot>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Tooltip",
        data: function() {
            return {
                show: false,
                tooltipDirection: this.direction,
            }
        },
        props: {
            direction: {default: 'top-center-direction'},
            addStyle: {default: ''},
            addClass: {default: ''},
            addClassWrap: {default: ''},
            addClassBtn: {default: ''},
            tempID: {default: ''},
            isChangeablePos: {
                default: function() {
                    return true;
                }
            },
            isBtnClose: {
                default: function () {
                    return true
                }
            },
            btnBig: {
                default: function () {
                    return false
                }
            },
            isPadding: {
                default: function () {
                    return true
                }
            },
            showOnHover:{
              default: function(){
                return false;
              }
            }
        },
        methods: {
            mouseLeave: function(){
              if(this.showOnHover){
                this.tooltipClose();
              }
            },
            mouseOver: function(){
              if(this.showOnHover){
                this.tooltipShow();
              }
            },
            btnClick: function() {
                if(this.show) {
                    this.tooltipClose();
                } else {
                    this.tooltipShow();
                }
            },
            tooltipClose: function() {
                let elem = this;
                this.show = false;

                setTimeout(() => {
                    this.$emit('tooltipclose', this.tempID);
                }, 100);
            },
            tooltipShow: function() {
                this.show = true;
                this.$emit('tooltipshow', this.tempID);
                this.tooltipDirection = this.direction;
                let self = this;
                setTimeout(function(){
                  let element = document.querySelector('.w-tooltip.opened .tooltip');
                  self.tooltipVisible(element);
                },10);
            },

           tooltipVisible: function (target) {
            //   
            var targetPosition = {
                top: window.pageYOffset + target.getBoundingClientRect().top,
                left: window.pageXOffset + target.getBoundingClientRect().left,
                right: window.pageXOffset + target.getBoundingClientRect().right,
                bottom: window.pageYOffset + target.getBoundingClientRect().bottom
              },
              //   
              windowPosition = {
                top: window.pageYOffset,
                left: window.pageXOffset,
                right: window.pageXOffset + document.documentElement.clientWidth,
                bottom: window.pageYOffset + document.documentElement.clientHeight
              };
            /* if (targetPosition.bottom > windowPosition.top && //          ,    
              targetPosition.top < windowPosition.bottom && //          ,    
              targetPosition.right > windowPosition.left && //          ,    
              targetPosition.left < windowPosition.right) {
              */
            if (windowPosition.bottom - targetPosition.top < target.offsetHeight && this.isChangeablePos) { //          ,    
                this.tooltipDirection = this.direction.replace('bottom', 'top');//'top-center-direction';
            }
          },

        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }


</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: opacity .15s;
    }
    .fade-popup-enter, .fade-popup-leave-to {
        opacity: 0;
    }
</style>
