import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    isLoad: false,
    isFormLoad: false,
    inspectionTmp: {}
}
const getters = {}
const mutations = {
    setLoad(state, data) {
        state.isLoad = data;
    },
    setFormLoad(state, data) {
        state.isFormLoad = data;
    },
    setTmpVersion(state, data) {
        state.inspectionTmp[data.entity_id] = data.text;
    },
    clearTmpVersion(state, data) {
        state.inspectionTmp[data.entity_id] = '';
    },
}
const actions = {
    setTmpVersion(context, data) {
        context.commit('setTmpVersion', data);
    },
    clearTmpVersion(context, data) {
        context.commit('clearTmpVersion', data);
    },
    setText(context, data) {
        return new Promise((resolve, reject) => {
            if (!state.isFormLoad) {
                context.commit('setFormLoad', true);

                let formData = new FormData();
                formData.append('text', data.text);

                axios.post(process.env.VUE_APP_API_PATH+'' +
                    '/moderator/inspection/',formData, {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                    params: {
                        entity_id: data.entity_id,
                        type: data.type,
                        status: 'text',
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setFormLoad', false);
                        if(data.type === 'action'){
                            if(data.isEdit){
                                context.commit('partnerActions/setInspectionText', {text:data.text},{root:true})
                            } else {
                                context.commit('moderatorCoupon/setInspectionText', {text:data.text},{root:true})
                            }

                        }
                        if(data.type === 'provider'){
                            if(data.isEdit){
                                context.commit('partner/setInspectionText', {text:data.text},{root:true})
                            } else {
                                context.commit('moderatorProviders/setInspectionText', {text:data.text},{root:true})
                            }

                        }
                        resolve();
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setFormLoad', false);
                });
            }
        });
    },
    setStatus(context, data) {
        return new Promise((resolve, reject) => {
            if (!state.isFormLoad) {
                context.commit('setFormLoad', true);

                let formData = new FormData();
                formData.append('text', data.text);

                axios.post(process.env.VUE_APP_API_PATH+'' +
                    '/moderator/inspection/',formData, {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                    params: {
                        entity_id: data.entity_id,
                        type: data.type,
                        status: data.status,
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setFormLoad', false);
                        if(data.type === 'action'){
                            if(data.isEdit){
                                context.commit('partnerActions/setInspectionText', {text:data.text, status: data.status},{root:true})
                            } else {
                                context.commit('moderatorCoupon/setInspectionText', {text:data.text, status: data.status},{root:true})
                            }

                        }
                        if(data.type === 'provider'){
                            if(data.isEdit){
                                context.commit('partner/setInspectionText', {text:data.text, status: data.status},{root:true})
                            } else {
                                context.commit('moderatorProviders/setInspectionText', {text:data.text, status: data.status},{root:true})
                            }
                        }
                        resolve();
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setFormLoad', false);
                });
            }
        });
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}