<template>
    <div v-if="successModerator && (isProviderEditLoaded || isCooperationEditLoaded || couponLoading || isRepaymentDetailLoaded)" class="h-editor-back">
        <router-link v-if="isCooperationEdit && isCooperationEditLoaded && cooperationBackName"
                     :to="'/moderator/cooperation/'+cooperationBackCode+'/'"
                     class="h-editor-link">
            <span class="i i-back"></span>
            <span class="t">{{cooperationBackName}}</span>
        </router-link>

        <router-link v-if="isProviderEdit && isProviderEditLoaded" :to="'/moderator/providers/'+providerBackCode+'/'" class="h-editor-link">
            <span class="i i-back"></span>
            <span class="t">{{providerBackName}}</span>
        </router-link>

        <router-link v-if="isCouponDetail && !couponLoading && actionBackName !== ''" :to="'/moderator/coupons/' + actionBackCode + '/'" class="h-editor-link">
            <span class="i i-back"></span>
            <span class="t">{{actionBackName}}</span>
        </router-link>

        <router-link v-if="isRepaymentDetail && isRepaymentDetailLoaded" :to="'/moderator/repayment/' + repaymentBackCode + '/'" class="h-editor-link">
            <span class="i i-back"></span>
            <span class="t">{{repaymentBackName}}</span>
        </router-link>

    </div>
    
</template>

<script>
    import {mapState} from 'vuex';
    export default {
        name: "HeaderEditorBack",
        computed: {
            ...mapState({
                user: state => state.users.user,
                providerListStatus: state => state.moderatorProviders.listStatus,
                repaymentListStatus: state => state.moderatorRepayment.listStatus,
                actionListStatus: state => state.moderatorCoupon.listStatus,
                cooperationListStatus: state => state.moderatorCooperation.listStatus,
                providerLoading: state => state.moderatorProviders.isLoad,
                couponLoading: state => state.moderatorCoupon.isLoadDetail,
                repaymentLoading: state => state.moderatorRepayment.isLoadDetail,
                providerItem: state => state.moderatorProviders.itemDetail,
                cooperationItem: state => state.moderatorCooperation.itemDetail,
                cooperationLoading: state => state.moderatorCooperation.isLoad,
                actionMenuList: state => state.menus.itemsAction,
                action: state => state.moderatorCoupon.itemDetail,
            }),
            actionBackCode() {

                let statusId = '';
                if(this.$route.params.action == 'inspection'){
                    statusId =  'inspection';
                }
                else if(this.$route.params.action == 'inspection-rework'){
                    statusId =  'inspection-rework'
                } else {
                    statusId = this.actionListStatus ? this.actionListStatus : this.action.status_moderator;

                    for (let itemIndex in this.actionMenuList['coupons']) {
                        if (this.actionMenuList['coupons'][itemIndex]['id'] == statusId) {
                            return this.actionMenuList['coupons'][itemIndex]['code'];
                        }
                    }
                }
                return statusId;
            },
            successModerator(){
                let result = true;
                if(this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0){
                    result = false;
                    switch (this.$route.name){
                        case 'repayment':
                        case 'repayment-detail':
                        case 'repayment-default':
                            if(this.user.permissionModerator.indexOf(19) !== -1){
                                result = true;
                            }
                            break;
                    }
                }
                return result
            },
            isCooperationEdit() {
                return this.$route.name === 'cooperation-edit'
            },
            isActionEditPage() {
                return this.$route.name === 'coupons-edit'
            },
            isCooperationEditLoaded() {
                if ((this.cooperationItem.length !== 0) && !this.cooperationLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isCouponDetail(){
                return this.$route.name === 'coupons-detail'
            },
            isRepaymentDetail(){
                return this.$route.name === 'repayment-detail'
            },
            isProviderEdit() {
                return this.$route.name === 'provider-edit'|| this.$route.name === 'provider-edit-section'
            },
            actionBackId() {
                let statusId = '';
                if(this.$route.params.action == 'inspection'){
                    statusId =  'inspection';
                }
                else if(this.$route.params.action == 'inspection-rework'){
                    statusId =  'inspection-rework'
                } else {
                    statusId = this.actionListStatus ? this.actionListStatus : this.action.status_moderator;
                }
                return statusId;
            },
            actionBackName(){
                if(this.$route.params.action == 'inspection'){
                    return ' ';
                }
                else if(this.$route.params.action == 'inspection-rework'){
                    return ' '
                } else {
                    let statusId = this.actionListStatus ? this.actionListStatus : this.action.status_moderator;

                    for (let itemIndex in this.actionMenuList['coupons']) {
                        if (this.actionMenuList['coupons'][itemIndex]['id'] == statusId) {
                            return this.actionMenuList['coupons'][itemIndex]['name']+" ";
                        }
                    }
                }

            },
            isProviderEditLoaded() {
                if (!this.providerLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isCouponOperationsLoaded(){
                if ((this.operations.length !== 0) && !this.couponLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isRepaymentDetailLoaded() {
                if ((this.repayment.length !== 0) && !this.repaymentLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            cooperationBackCode() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.cooperationItem['status'];

                for (let itemIndex in this.actionMenuList['cooperation']) {
                    if (this.actionMenuList['cooperation'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['cooperation'][itemIndex]['code'];
                    }
                }
            },
            cooperationBackId() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.cooperationItem['status'];

                return statusId;
            },
            cooperationBackName() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.cooperationItem['status'];

                for (let itemIndex in this.actionMenuList['cooperation']) {
                    if (this.actionMenuList['cooperation'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['cooperation'][itemIndex]['name'] + ' ';
                    }
                }
            },
            providerBackCode() {
                if( this.providerItem['statusCode'] != 'verify_passive'){
                    if(this.$route.params.action == 'inspection'){
                        return 'inspection';
                    }
                    else if(this.$route.params.action == 'inspection-rework'){
                        return 'inspection-rework';
                    }
                    else if(this.providerBackId == 11){
                        return 'wait_contract';
                    }
                    else {
                        let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                        for (let itemIndex in this.actionMenuList['providers']) {
                            if (this.actionMenuList['providers'][itemIndex]['id'] == statusId) {
                                return this.actionMenuList['providers'][itemIndex]['code'];
                            }
                        }
                    }

                } else {
                    return 'verify_passive';
                }
            },
            providerBackId() {
                let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                return statusId;
            },
            providerBackName() {

                if( this.providerItem['statusCode'] != 'verify_passive') {
                    if(this.$route.params.action == 'inspection'){
                        return ' ';
                    }
                    else if(this.$route.params.action == 'inspection-rework'){
                        return ' ';
                    }
                    else if(this.providerBackId == 11){
                        return ' '
                    } else {
                        let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                        for (let itemIndex in this.actionMenuList['providers']) {
                            if (this.actionMenuList['providers'][itemIndex]['id'] == statusId) {
                                return this.actionMenuList['providers'][itemIndex]['title'];
                            }
                        }
                    }

                } else {
                    return ' ';
                }
            },
        }
    }
</script>

