import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    sections: [],
    selectSections: [],
    topSections: [],
    items: [],
    isLoadSections: false,
    isLoadItems: false,
    isLoadNextPage: true,
    itemsPage: 1,
    editSection: [],
    editItem: [],
    previousSection: {
        top: {
           id: false
        } ,
        second: {
            id: false
        }
    },
    isSearch: false,
    isSearchPrev: false,
    searchStr: '',
    rolled: false,
    deactivatedItems: 0,
    canDrag: false,
    marks: [],
    tumblers: {
        editable: {
            value: false,
            name: 'editable',
        },
        notify: {
            value: false,
            name: 'notify',
        },
        staffEditable: {
            value: false,
            name: 'staffEditable',
        },
        lock: {
            value: false,
            name: 'finally',    
        },
        finally: {
            value: false,
            name: 'finally',
        },
    },
    readerRights:{

    },
    writerRights: {
        'subsectionAddEdit': true,
        'subsectionDrag': false,
        'ruleEdit': true,
        'ruleAdd': true,
        'deletedShow': false,
        'ruleDeactivate': true,
        'staffSettings': true,
        'logsShow': true,
    },
    blockEdit: {
        status: false,
        user: {},
        action: false,
    },
    blockEditTimer: false,
    openSearchField: false,
    hasSelectedSection: false,
    sectionsAxiosCancelTokenSource: false
}
const getters = {
    getTopSections(state){
        let items = [];
        for(let i in state.topSections){
            let item = {...state.topSections[i]}
            item.updated = !!state.marks.find(sect => sect.macro === item.id);
            items.push(item);
        }
        return items
    },
    getSections(state){
        let items = [];
        for(let i in state.sections){
            let item = {...state.sections[i]}
            item.updated = !!state.marks.find(sect => sect.section === item.id);
            items.push(item);
        }
        return items
    },
    getSelectSections(state){
        return state.selectSections
    },
    getItems(state){
        let items = [];
        for(let i in state.items){
            let item = {...state.items[i]}
            item.updated = item.active && !!state.marks.find(sect => sect.rule === item.id);
            items.push(item);
        }
        return items
    },
    getItemFiles(state){
        return state.editItem.files
    },
    getCurrentTopSect: state => id => {
        return state.topSections.find(sect => sect.id === id);
    }
}
const mutations = {
    setSubtitle(state, data) {
        let activeIndex = false;

        state.topSections.forEach((sect, index) => {
            if (sect.id === window.app.$route.params.id) {
                activeIndex = index;
            }
        });

        Vue.set(state.topSections[activeIndex],'subtitle', data);
    },
    setSelectedSection(state, data) {
        Vue.set(state,'hasSelectedSection', data);
    },
    setPreviousSection(state, data) {
        Vue.set(state.previousSection.top,'id', data.topSection);
        Vue.set(state.previousSection.second,'id', data.section);
    },

    openSearchField(state, data){
        Vue.set(state,'openSearchField', data);
    },


    setTopSections(state, data){
        if (typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'topSections', data.items);
        }
    },
    toggleTumbler(state, data){
        state.tumblers[data].value = !state.tumblers[data].value;
    },
    tumblerOff(state, data){
        state.tumblers[data].value = false;
    },
    removeRule(state, data){
        for (let i in state.items) {
            if(state.items[i].id == data){
                state.items.splice(i, 1);
            }
        }
    },
    setCheckEditLoader(state, data){
        for (let i in state.items) {
            if(state.items[i].id == data.id){
                state.items[i].isCheckEdit = data.value;
            }
        }
    },
    changeRule(state, data) {
        for (let i in state.items) {
            if(state.items[i].id == data.id){
                data.opened = state.items[i].opened;
                Vue.set(state.items, i, data);
            }
        }
    },
    changeFiles(state, data) {
        state.editItem.files = data;
    },
    addRule(state, data) {
        state.items.push(data);
    },
    setItems(state, data) {
        if (typeof data === 'object' && data.items !== undefined) {
            //Vue.set(state,'items', data.items);
            for (let itemIndex in data.items) {
                let item = data.items[itemIndex];
                state.items.push(item);
            }
        }
    },
    setItem(state, data) {
        if (typeof data === 'object' && data.items !== undefined) {
            let index = state.items.findIndex(sect => sect.id === data.items.id);
            Vue.set(state.items, index, data.items);
        }
    },
    setItemsLocal(state, data) {
        state.items = data.items;
    },
    setMark(state, data) {
        state.marks = data.items;
    },
    setSections(state, data) {
        if (typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'sections', data.items);
            Vue.set(state,'selectSections', data.items);
        }
    },
    setItemFiles(state, data) {
        if (typeof data === 'object' && data.items !== undefined) {
            Vue.set(state.editItem,'files', data.items);
        }
    },
    setLoadItems(state, data) {
        state.isLoadItems = data;
    },
    setLoadSections(state, data) {
        state.isLoadSections = data;
    },
    setSectionsAxiosCancelTokenSource(state, data) {
        state.sectionsAxiosCancelTokenSource = data;
    },
    setEditSection(state, data) {
        state.editSection = data;
    },
    setEditItem(state, data) {
        Vue.set(state.tumblers.finally, 'value', data.finally);
        Vue.set(state, 'editItem', data);
    },
    setNewPage(state) {
        Vue.set(state, 'items', []);
        state.itemsPage = 1;
        state.isLoadNextPage = true;
    },
    incrementPage(state) {
        state.itemsPage++;
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    removeFile(state, data) {
        if(state.editItem.files){
            for(let f in state.editItem.files){
                if(state.editItem.files[f].id === data){
                    state.editItem.files.splice(f , 1);
                }
            }
        }
    },
    addFile(state, data) {
        let files = [...state.editItem.files] ? [...state.editItem.files] : []
        files.push(data)
        Vue.set(state.editItem,'files', files);
    },
    setIsSearch(state, data){
        state.isSearch = data;
    },
    setIsSearchPrev(state, data){
        state.isSearchPrev = data;
    },
    toggleRule(state, data){
        state.items[data].opened = !state.items[data].opened;
    },
    openRule(state, data){
        //state.items[data].opened = !state.items[data].opened;

        for (let i in state.items) {
            if(state.items[i].id == data){
                state.items[i].opened = true;
                //Vue.set(state.items, i, data);
            }
        }
    },
    toggleRolled(state){
        state.rolled = false;
    },
    removeSection(state, data){
        for (let i in state.sections) {
            if(state.sections[i].id == data){
                state.sections.splice(i, 1);
            }
        }
    },
    removeTopSection(state, data){
        for (let i in state.topSections) {
            if(state.topSections[i].id == data){
                state.topSections.splice(i, 1);
            }
        }
    },
    deleteMark(state, data){
        for (let i in state.marks) {
            if(state.marks[i].rule == data){
                state.marks.splice(i, 1);
            }
        }
    },
    setDeactivatedItems(state, data){
        state.deactivatedItems = data;
    },
    setSearchStr(state, data){
        state.searchStr = data;
    },
    toggleDrag(state, data){
        state.canDrag = !state.canDrag;
    },
    setTimerBlockEdit(state, data){
        state.blockEditTimer = data;
    },
    setBlockEdit(state,data){
        state.blockEdit = data;
    },
    clearBlockEdit(state,data){
        if(state.blockEditTimer){
            clearInterval(state.blockEditTimer);
        }
        state.blockEdit = {
            status: false,
            user: {},
            action: false,
        };
    },
    setItemTextLoad(state, data){
        if (typeof(data.index) !== 'undefined') {
            state.items[data.index].texLoading = data.status;
        }
    },
    addChildId(state, data) {
        for (let i in state.topSections) {
            if(state.topSections[i].id == data.pid){
                state.topSections[i].subsections.push(data.id + '');
            }
        }
    }
}
const actions = {
    setEditItem(context, data) {
        return new Promise((resolve, reject) => {
            context.commit('setEditItem', data);
            resolve();
        });
    },
    getTopSection(context, data) {
        return new Promise((resolve, reject) => {
            //context.commit('setSections', {items: []});
            //if (!state.isLoadSections) {
                    context.commit('setLoadSections', true);

                    let params =  {
                        type: 'topSections',
                    };

                    axios.get(process.env.VUE_APP_API_PATH + '/rules/section/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        context.commit('setLoadSections', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setTopSections', response.data.result);
                            if(response.data.result.deactivatedEls){
                                context.commit('setDeactivatedItems', response.data.result.deactivatedEls);
                            }
                        } else {
                            if (+response.data.error === 401) {
                                context.commit('auth/setIsNoSuccessSection', {}, {root: true})
                            }
                        }

                        resolve();
                    }).catch(function (error) {
                        context.commit('setLoadSections', false);
                        reject();
                    });
            //}
        });
    },
    getMarks(context, data) {
        return new Promise((resolve, reject) => {

            axios.get(process.env.VUE_APP_API_PATH + '/rules/mark/', {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setMark', response.data.result);
                }
                resolve();
            }).catch(function (error) {
                reject();
            });
        });
    },
    checkEditItem(context, data) {
        return new Promise((resolve, reject) => {
            axios.get(process.env.VUE_APP_API_PATH + '/rules/element/'+data.id+'/', {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true,
                params: {
                    type: "checkBlockEdit"
                },
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setBlockEdit', response.data.result);
                    resolve(response.data.result);
                } else {
                    resolve(state.blockEdit);
                }
            }).catch(function (error) {
                reject();
            });
        });
    },
    setTimerBlockEdit(context, data){
        axios.put(process.env.VUE_APP_API_PATH+'/rules/element/' + data.id,{}, {
            withCredentials: true,
            headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            params: {
                type: "setBlockEdit"
            },
            paramsSerializer: function (params) {
                return qs.stringify(params, {arrayFormat: 'repeat'})
            },
        })
        let blockEditTimer = setInterval(()=>{
            axios.put(process.env.VUE_APP_API_PATH+'/rules/element/' + data.id,{}, {
                withCredentials: true,
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                params: {
                    type: "setBlockEdit"
                },
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            })
        },60000);
        context.commit('setTimerBlockEdit', blockEditTimer);
    },
    clearBlockEdit(context, data){
        return new Promise((resolve, reject) => {
            context.commit('clearBlockEdit');
            axios.put(process.env.VUE_APP_API_PATH+'/rules/element/' + data.id, {},{
                withCredentials: true,
                params: {
                    type: "clearBlockEdit"
                },
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            });
        })
    },
    deleteMarks(context, data) {
        return new Promise((resolve, reject) => {
            context.commit('deleteMark', data.id);
            axios.delete(process.env.VUE_APP_API_PATH + '/rules/mark/'+data.id+'/', {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true
            }).then(response => {
                resolve();
            }).catch(function (error) {
                reject();
            });
        });
    },
    getSubsectionOption(context, data){
        return new Promise((resolve, reject) => {
            //if (!state.isLoadSections) {
                /*if (data.reload) {
                    context.commit('setNewPage', {status: data.status});
                }*/
                //if (state.isLoadNextPage) {
                    //context.commit('setLoadSections', true);

                    let params =  {
                        type: 'secondSections',
                        id: data.id,
                        //page: state.page,
                        //pageCount: state.backData.pageCount
                    };

                    axios.get(process.env.VUE_APP_API_PATH + '/rules/section/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        //context.commit('setLoadSections', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            resolve(response.data.result.items);
                            //context.commit('setSections', response.data.result);
                            //context.commit('setDeactivatedItems', response.data.result.deactivatedEls);
                            //context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        } else {
                            if (+response.data.error === 401) {
                                context.commit('auth/setIsNoSuccessSection', {}, {root: true})
                            }
                        }
                        //context.commit('incrementPage');
                        resolve();
                    }).catch(function (error) {
                        //context.commit('setLoadSections', false);
                        reject();
                    });
                //}
            //}
        });
    },
    getSections(context, data) {
        return new Promise((resolve, reject) => {
            context.commit('setSections', {items: []});

            if (state.isLoadSections) {
                context.commit('setLoadSections', false);
                if (state.sectionsAxiosCancelTokenSource !== false) {
                    state.sectionsAxiosCancelTokenSource.cancel();
                    //console.log('cancel');
                }
            }

            if (!state.isLoadSections) {
                /*if (data.reload) {
                    context.commit('setNewPage', {status: data.status});
                }*/
                //if (state.isLoadNextPage) {
                    context.commit('setLoadSections', true);

                    let params =  {
                        type: 'secondSections',
                        id: data.id,
                        //page: state.page,
                        //pageCount: state.backData.pageCount
                    };

                    let request = axios.CancelToken.source();
                    context.commit('setSectionsAxiosCancelTokenSource', request);

                    axios.get(process.env.VUE_APP_API_PATH + '/rules/section/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        cancelToken: state.sectionsAxiosCancelTokenSource.token,
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        context.commit('setSectionsAxiosCancelTokenSource', false);
                        context.commit('setLoadSections', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setSections', response.data.result);
                            if(response.data.result.deactivatedEls){
                                context.commit('setDeactivatedItems', response.data.result.deactivatedEls);
                            }
                            //context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        } else {
                            if (+response.data.error === 401) {
                                context.commit('auth/setIsNoSuccessSection', {}, {root: true})
                            }
                        }
                        //context.commit('incrementPage');
                        resolve();
                    }).catch(function (error) {
                        context.commit('setLoadSections', false);
                        reject();
                    });
                //}
            }
        });
    },

    getDeactivatedItems(context, data){
        return new Promise((resolve, reject) => {
            context.commit('setLoadSections', true);
            let params =  {
                type: 'deactivatedItems',
            };
            axios.get(process.env.VUE_APP_API_PATH + '/rules/section/', {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true,
                params: params,
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    //context.commit('setDeactivatedItems', response.data.result);
                } else {
                    if (+response.data.error === 401) {
                        context.commit('auth/setIsNoSuccessSection', {}, {root: true})
                    }
                }
                //context.commit('incrementPage');
                resolve();
            }).catch(function (error) {
                reject();
            });
        });
    },
    setSection(context, data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/section/';
            if(data.id){
                url +=  data.id+'/';
            }

            let formData = new FormData();
            formData.append('name', data.name);
            formData.append('topSection', data.topSection);
            formData.append('type', 'saveSection');

            axios.post(url,formData, {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
            }).then(response => {
                let id  = +response.data.result.done > 0 ? +response.data.result.done : false;
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    if(data.isTop){
                        context.dispatch('getTopSection');
                    }
                    else{
                        //if(data.activeTopSection == data.topSection){
                            context.dispatch('getSections',{id: data.topSection});

                            if (id) {
                                //  id  topSection
                                context.commit('rules/addChildId', {pid: data.topSection, id: response.data.result.done}, {root: true})
                            }
                        //}
                    }

                    resolve(id);
                } else {
                    if( +response.data.error===401){
                        //context.dispatch('auth/logout',{},{root:true});
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    removeSection(context, data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/section/'+data.id+'/';
            let formData = new FormData();
            formData.append('type', 'removeSection');
            formData.append('topSection', data.topSection);

            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(response.data.result){
                    context.commit('removeSection',data.id);
                    resolve(true);
                }else{
                    resolve(false);
                }
            }).catch(function (error) {
                resolve();
            });
        });
    },
    removeTopSection(context, data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/section/'+data.id+'/';
            let formData = new FormData();
            formData.append('type', 'removeTopSection');
            //formData.append('topSection', data.topSection);

            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(response.data.result){
                    context.commit('removeTopSection',data.id);
                    resolve(true);
                }else{
                    resolve(false);
                }
            }).catch(function (error) {
                resolve();
            });
        });
    },
    setSectionsSort(context){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/section/';
            let formData = new FormData();
            let ids = [];
            formData.append('type', 'setSectionsSort');

            context.commit('setSubtitle', context.state.sections[0].name);

            for (let i in context.state.sections) {
                ids.push(context.state.sections[i].id);
            }
            formData.append('ids', JSON.stringify(ids));
            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve();
            }).catch(function (error) {
                resolve();
            });
        });
    },
    setTopSectionsSort(context){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/section/';
            let formData = new FormData();
            let ids = [];
            formData.append('type', 'setTopSectionsSort');
            for (let i in context.state.topSections) {
                ids.push(context.state.topSections[i].id);
            }
            formData.append('ids', JSON.stringify(ids));
            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve();
            }).catch(function (error) {
                resolve();
            });
        });
    },
    setRulesSort(context,data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/element/';
            let formData = new FormData();
            let ids = [];
            formData.append('type', 'setRulesSort');
            for (let i in context.state.items) {
                ids.push(context.state.items[i].id);
            }
            if(data.isRemoved){
                formData.append('isRemoved', 'y');
            }
            formData.append('ids', JSON.stringify(ids));
            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve();
            }).catch(function (error) {
                resolve();
            });
        });
    },
    removeRule(context, data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/element/'+data.id+'/';
            let formData = new FormData();
            formData.append('type', 'removeRule');
            formData.append('forever', data.forever);
            formData.append('section', data.section);
            formData.append('subSection', data.subSection);

            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                let isLast = false;
                context.commit('removeRule',data.id);

                if(response.data.result.deactivatedEls !== undefined){
                    context.commit('setDeactivatedItems', response.data.result.deactivatedEls);
                    if(data.forever && response.data.result.deactivatedEls==0){
                        isLast = true;
                    }
                } else if(state.rules.deactivatedItems == 0){
                    isLast = true;
                }
                resolve(isLast);
            }).catch(function (error) {
                resolve();
            });
        });
    },
    removeRuleAll(context, data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/element/';
            let formData = new FormData();
            formData.append('type', 'removeRuleAll');

            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(response.data.result.deactivatedEls !== undefined){
                    context.commit('setDeactivatedItems', response.data.result.deactivatedEls);
                }
                resolve();
            }).catch(function (error) {
                resolve();
            });
        });
    },
    addScreenShot(context, data){
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/element/';
            let formData = new FormData();
            formData.append('type', 'addScreenShot');
            formData.append('screen', data.value);

            axios.post(url, formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                context.commit('addFile', response.data.result.file);
                resolve(response.data.result.file);

            }).catch(function (error) {
                resolve();
            });
        });
    },
    uploadFiles(context, data) {
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/element/';
            axios.post(url,data.formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    for (let f in response.data.result.files){
                        context.commit('addFile', response.data.result.files[f]);
                    }
                    resolve(response.data.result.files);
                } else {
                    if( +response.data.error===401){
                        //context.dispatch('auth/logout',{},{root:true});
                    }
                }
            }).catch(function (error) {
                console.log(error);
                resolve();
            });
        });
    },
    setRule(context, data) {
        return new Promise((resolve, reject) => {
            let url = process.env.VUE_APP_API_PATH+'/rules/element/';
            if(data.id){
                url +=  data.id+'/';
            }
            axios.post(url,data.formData, {
                headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    //context.dispatch('getSections');
                    if(data.id){
                        if(response.data.result.item.section == data.activeSection){
                            context.commit('changeRule',response.data.result.item);
                        }
                        else{
                            context.commit('removeRule',response.data.result.item.id);
                        }
                    }
                    else{
                        if(response.data.result.item.section == data.activeSection){
                            context.commit('addRule',response.data.result.item);
                        }
                    }
                    resolve();
                    context.commit('setIsSearch', false);
                    context.commit('setSearchStr', '');
                } else {
                    if( +response.data.error===401){
                        //context.dispatch('auth/logout',{},{root:true});
                    }
                }
            }).catch(function (error) {
                console.log(error);
                resolve();
            });
        });
    },
    getItems(context, data) {
        return new Promise((resolve, reject) => {
            //context.commit('setItems', {items: []});
            if (!state.isLoadItems) {
                if (data.reload) {
                    context.commit('setNewPage', {status: data.status});
                }
                if (state.isLoadNextPage) {
                    context.commit('setLoadItems', true);

                    let params = {};

                    if (data.search) {
                        context.commit('setIsSearch', true);
                        context.commit('setSearchStr', data.search);
                        params.status = data.status;
                        params.search = data.search;
                    }
                    else{
                        if(!state.isSearchPrev){
                            context.commit('setIsSearch', false);
                            context.commit('setSearchStr', '');
                        }

                        params =  {
                            type: 'items',
                            page: state.itemsPage,
                            topsect: data.topSect,
                            //pageCount: state.backData.pageCount
                        };
                        if(data.full){
                            params.full = true;
                        }
                    }

                    axios.get(process.env.VUE_APP_API_PATH + '/rules/element/'+data.id+'/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setItems', response.data.result);
                            context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        } else {
                            if (+response.data.error === 401) {
                                context.commit('auth/setIsNoSuccessSection', {}, {root: true})
                            }
                        }
                        context.commit('setLoadItems', false);
                        context.commit('incrementPage');
                        resolve();
                    }).catch(function (error) {
                        context.commit('setLoadItems', false);
                        reject();
                    });
                }
            }
        });
    },
    getRuleText(context, data) {
        return new Promise((resolve, reject) => {
            if(data.item.text != false){
                resolve();
            }
            else{
                context.commit('setItemTextLoad', {index: data.index, status: true});
                let params =  {
                    type: 'getItem',
                    removed: data.removed ? 'y': '',
                    search: context.state.searchStr
                };
                axios.get(process.env.VUE_APP_API_PATH + '/rules/element/'+data.item.id+'/', {
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                    withCredentials: true,
                    params: params,
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    context.commit('setItem', response.data.result);
                    context.commit('setItemTextLoad', {index: data.index, status: false});
                    resolve(response.data.result.items);
                }).catch(function (error) {
                    context.commit('setItemTextLoad', {index: data.index, status: false});
                    reject();
                });
            }
        });
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
